<template>
  <div class="enterprise-info">
    <register-info-title-fill class="personal-info" title="个人信息">
      <register-info :infoList="personalInfoList"></register-info>
    </register-info-title-fill>
    <register-info-title-fill class="company-info" title="机构信息">
      <register-info :infoList="companyInfoList" registerBtn></register-info>
    </register-info-title-fill>
  </div>
</template>

<script>
import { RegisterInfoTitleFill, RegisterInfo } from "./components";

export default {
  name: "RegisterThirdParty",
  components: {
    RegisterInfoTitleFill,
    RegisterInfo
  },
  data() {
    return {
      width: "15%",
      gender: "0",
      identity: "0",
      personalInfoList: [
        {
          id: 1,
          type: "input",
          title: "姓名",
          class: "name",
          placeholder: "请填写您的真实姓名"
        },
        {
          id: 2,
          type: "radio",
          title: "性别",
          class: "gender",
          model: "0",
          data: [
            {
              id: 1,
              label: "0",
              content: "男"
            },
            {
              id: 2,
              label: "1",
              content: "女"
            }
          ]
        },
        {
          id: 3,
          type: "input",
          title: "手机",
          class: "phone",
          placeholder: "请填写您的手机号码"
        },
        {
          id: 4,
          type: "input",
          title: "职位",
          class: "position",
          placeholder: "请填写您的职位(如:大学教授)"
        }
      ],
      companyInfoList: [
        {
          id: 1,
          type: "input",
          title: "机构全称",
          class: "company-name",
          placeholder: "请填写您所在机构的全称(如:深圳大学)"
        },
        {
          id: 2,
          type: "input",
          title: "注册地",
          class: "company-address",
          placeholder: "请填写您机构的注册地点(如:深圳市南山区)"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
@import "./components/mixin.scss";

.enterprise-info {
  @extend .e-register-info;
  .company-info {
    margin-top: 40px;
  }
}
</style>
