<template>
  <el-dialog
    class="home-login-dialog"
    title="登录"
    :visible.sync="loginDialogVisible"
    width="30%"
    center
    :before-close="handleDisplayChange"
  >
    <title-fill class="email" title="邮箱" width="10%">
      <el-input
        class="input"
        v-model="email"
        placeholder="请输入邮箱"
        clearable
      />
    </title-fill>
    <title-fill class="pwd" title="邮箱" width="10%">
      <el-input
        class="input"
        v-model="pwd"
        placeholder="请输入密码"
        show-password
      />
    </title-fill>
    <div class="login">
      <el-button type="primary" size="small" @click="handleDisplayChange"
        >登录</el-button
      >
    </div>
    <div class="register">
      <el-button size="small" @click="handleDisplayChange">注册</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { TitleFill } from "@/components";

export default {
  name: "HomeLoginDialog",
  components: {
    TitleFill
  },
  props: {
    loginDisplay: Boolean
  },
  data() {
    return {
      loginDialogVisible: false,
      email: "",
      pwd: ""
    };
  },
  methods: {
    handleDisplayChange() {
      this.loginDialogVisible = false;
      this.$emit("changeDisplayState");
    }
  },
  watch: {
    loginDisplay(val) {
      this.loginDialogVisible = val;
    }
  }
};
</script>

<style lang="scss" scoped>
.home-login-dialog {
  $padding: 5%;
  .email,
  .pwd {
    @include flex(all, center, center);
    padding: 0 $padding;
    &-text {
      width: 15%;
    }
    .input {
      flex: 1;
    }
  }
  .pwd {
    margin: 20px 0 30px;
  }
  .login {
    margin-bottom: 15px;
  }
  .login,
  .register {
    text-align: center;
    & > button {
      width: 60%;
    }
  }
}
</style>
