import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/home/Home";
import ThirdPartyHome from "@/views/third-party/third-party-home/ThirdPartyHome";
import VideoRecord from "@/views/third-party/videoRecord/VideoRecord";
import ComponentTest from "@/views/test/ComponentTest";
import {
  RegisterCandidate,
  RegisterEnterprise,
  RegisterThirdParty,
  RegisterSuccess,
  Register,
  RegisterMain
} from "@/views/register";
import Candidate from "@/views/candidate/Candidate";
import CandidateHome from "@/views/candidate/CandidateHome";
import CandidateCenter from "@/views/candidate/CandidateCenter";
import Interviewer from "@/views/interviewer/Interviewer";
import {
  InterviewerManagement,
  InterviewerQuestion,
  InterviewerPersonalCenter,
  InterviewerRecord
} from "@/views/interviewer/components";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/test",
    name: "componentTest",
    component: ComponentTest
  },
  {
    path: "/register",
    component: Register,
    children: [
      {
        path: "",
        name: "register",
        component: RegisterMain
      },
      {
        path: "candidate",
        name: "register-candidate",
        component: RegisterCandidate
      },
      {
        path: "enterprise",
        name: "register-enterprise",
        component: RegisterEnterprise
      },
      {
        path: "third-party",
        name: "register-third-party",
        component: RegisterThirdParty
      },
      {
        path: "success",
        name: "success",
        component: RegisterSuccess
      }
    ]
  },
  {
    path: "/candidate",
    component: Candidate,
    children: [
      {
        path: "",
        name: "candidate-home",
        component: CandidateHome
      },
      {
        path: "center",
        name: "candidate-center",
        component: CandidateCenter
      }
    ]
  },
  {
    path: "/third-party-home",
    name: "third-party-home",
    component: ThirdPartyHome
  },
  {
    path: "/videoRecord",
    name: "videoRecord",
    component: VideoRecord
  },
  {
    path: "/interviewer",
    component: Interviewer,
    children: [
      {
        path: "",
        name: "interviewer",
        component: InterviewerManagement
      },
      {
        path: "question",
        name: "question",
        component: InterviewerQuestion
      },
      {
        path: "personal-center",
        name: "personal-center",
        component: InterviewerPersonalCenter
      },
      {
        path: "record",
        name: "record",
        component: InterviewerRecord
      }
    ]
  }
];
// {
//   path: "/about",
//   name: "about",
//   // route level code-splitting
//   // this generates a separate chunk (about.[hash].js) for this route
//   // which is lazy-loaded when the route is visited.
//   component: () =>
//     import(/* webpackChunkName: "about" */ "../views/About.vue")
// }

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
