      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "Select",
  components: {},
  props: {
    options: Array,
    placeholder: String
  },
  data() {
    return {
      value: ""
    };
  }
};
</script>

