import Vue from "vue";
import {
  Button,
  Input,
  Card,
  Pagination,
  Select,
  Option,
  Dialog,
  Radio,
  Avatar,
  Cascader,
  Upload,
  DatePicker,
  Menu,
  MenuItem,
  Table,
  TableColumn,
  Switch
} from "element-ui";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "style/index.scss";

Vue.config.productionTip = false;
Vue.use(Button);
Vue.use(Input);
Vue.use(Card);
Vue.use(Pagination);
Vue.use(Select);
Vue.use(Option);
Vue.use(Dialog);
Vue.use(Radio);
Vue.use(Avatar);
Vue.use(Cascader);
Vue.use(Upload);
Vue.use(DatePicker);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Switch);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
