<template>
  <div class="register-main">
    <div class="identity">
      <el-radio v-model="radio" label="0">求职者</el-radio>
      <el-radio v-model="radio" label="1">企业</el-radio>
      <el-radio v-model="radio" label="2">第三方</el-radio>
    </div>
    <el-input class="email" placeholder="邮箱" />
    <div class="verify-code">
      <el-input placeholder="验证码" />
      <el-button class="get-code">获取验证码</el-button>
    </div>
    <el-input class="pwd" placeholder="密码，不少于6位" />
    <el-input class="check-pwd" placeholder="确认密码" />
    <el-button class="next-btn" type="primary" @click="handleNext">
      下一步
    </el-button>
  </div>
</template>

<script>
export default {
  name: "RegisterMain",
  components: {},
  data() {
    return {
      radio: "0"
    };
  },
  methods: {
    handleNext() {
      let identityMap = [
        {
          path: "candidate",
          name: "求职者注册"
        },
        {
          path: "enterprise",
          name: "企业注册"
        },
        {
          path: "third-party",
          name: "第三方注册"
        }
      ];
      this.$router.push(`/register/${identityMap[this.radio].path}`);
    }
  }
};
</script>

<style lang="scss" scoped>
.register-main {
  margin-top: 40px;
  @include flex(align-items, center);
  flex-direction: column;
  .identity {
    text-align: center;
  }
  .identity,
  .email,
  .verify-code,
  .pwd,
  .check-pwd {
    width: 40%;
  }

  .identity,
  .email,
  .verify-code,
  .pwd,
  .check-pwd,
  .next-btn {
    margin-bottom: 30px;
  }
  .verify-code {
    @include flex(justify-content, space-between);
    .get-code {
      width: 30%;
      margin-left: 10px;
    }
  }
  .next-btn {
    width: $extraLargeBW;
  }
}
</style>
